<template>
  <Modal
    v-model="ClassifyShow"
    :title="actionType === 'add' ? ' / ' : ' / '"
    :closable="false">
    <Form
      :label-width="100"
      ref="classifyForm"
      :model="projectData"
      :rules="rulesValid"
      label-position="left">
      <FormItem
        label=":"
        prop="name">
        <Input
          v-model="projectData.name"
          placeholder=""
          :disabled="actionType === 'modify' && projectData.name === '' || projectData.name === ''"></Input>
      </FormItem>
      <FormItem label=":">
        <Input
          v-model="projectData.description"
          type="textarea"
          placeholder=""></Input>
      </FormItem>
    </Form>
    <div slot="footer">
      <Button
        type="text"
        size="large"
        @click="Cancel"></Button>
      <Button
        type="primary"
        size="large"
        @click="Ok"></Button>
    </div>
  </Modal>
</template>
<script>
export default {
  props: {
    projectClassify: {
      type: Object,
      default: null,
    },
    addClassifyShow: {
      type: Boolean,
      defalut: null,
    },
    actionType: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      ClassifyShow: this.addClassifyShow,
      rulesValid: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { message: '20', max: 20 },
          { type: 'string', pattern: /^[a-zA-Z\u4e00-\u9fa5][a-zA-Z0-9_\u4e00-\u9fa5]*$/, message: '', trigger: 'blur' },
        ],
      },
    };
  },
  computed: {
    projectData() {
      return this.projectClassify;
    },
  },
  watch: {
    ClassifyShow(val) {
      this.$emit('showModal', val);
      if (!val) {
        this.$refs.classifyForm.resetFields();
      }
    },
    addClassifyShow(val) {
      this.ClassifyShow = val;
    },
  },
  methods: {
    Ok() {
      this.$refs.classifyForm.validate((valid) => {
        if (valid) {
          this.$emit('addProjectClassifyConfirm', this.projectData);
          this.ClassifyShow = false;
        } else {
          this.$Message.warning('');
        }
      });
    },
    Cancel() {
      this.ClassifyShow = false;
    },
  },
};
</script>
